@import url(https://fonts.googleapis.com/css?family=Charm);
@import url(https://fonts.googleapis.com/css?family=Quicksand);
@import url(https://fonts.googleapis.com/css?family=Railway);
@import url(https://fonts.googleapis.com/css?family=Satisfy);
@import url(https://fonts.googleapis.com/css?family=Merienda);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.gameScreen {
	border: solid 1px #b3b3b3;
	margin: 0px auto;
	/* margin-top: 25px; */
	background-color: #ffffff;
	box-shadow: 2px 2px 10px 5px #b3b3b3b6;
	-webkit-transition: 0.7s;
	transition: 0.7s;
}

.danger {
	background-color: #ff4d4d!important;
	border: solid 3px #bbbbbb;
}

.score {
	font-weight: bold;
}
body, html {
  height: 130%;
  background-image: -webkit-linear-gradient(top left, rgb(140, 0, 255), rgb(0, 195, 255));
  background-image: linear-gradient(to bottom right, rgb(140, 0, 255), rgb(0, 195, 255));  
  background-size: 100% 130%;
  background-repeat: no-repeat;
}

.App {
  text-align: center;
}

.title {
  font-size: 28px;
  font-family: 'Charm', cursive;
}

.nav-link {
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.active {
  font-weight: bold;
}

.nav-link:hover {
  font-size: 20px;
}

nav {
  min-height: 100px;
}

input#input-name {
  background-color: transparent;
  border: solid 0px #fff;
  outline: none!important;
  width: 100%;
  color: rgb(255, 255, 255)!important;
  border-bottom: solid 2px rgb(190, 116, 4);
}

input#input-name:focus {
  background-color: transparent!important;
  outline: none!important;
}

input#input-name::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.568);
}

input#input-name::-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.568);
}

input#input-name::placeholder {
  color: rgba(255, 255, 255, 0.568);
}

form {
  margin-top: 260px;
}

.App-logo {
  /* animation: rotate infinite 20s linear; */
  width: 300px;
  float: left;
}

@media screen and (min-width: 600px) {
  .App-logo {
    width: 500px;
    float: right;
  }
  body, html {
    height: 100%;
  }
}

@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#welcome {
  margin-top: 120px;
  text-align: left;
  color: rgb(255, 255, 255);
}

#welcome-title {
  font-family: 'Quicksand', sans-serif;
  font-size: 45px;
  font-weight: 50;

}

.welcome-body {
  color: rgba(255, 255, 255, 0.726);
  font-size: 18px;
  font-family: 'Merienda', cursive;
  font-weight: 50;
  margin-bottom: 2px;
}

.Try {
  background-color: rgba(0, 192, 16, 0.836);
  color: white;
}


.Try:hover {
  box-shadow: 2px 2px 10px 5px #b3b3b3b6;
}

#game {
  background-color: rgba(255, 255, 255, 0.623);
}
